

* {
  box-sizing: border-box;

}
body {
  background-color:white;
}
.todo-app {
  display: flex;
  flex-direction: column;
  justify-content: start;
  width: 520px;
  min-height: 500px;
  background: #000000;
  text-align: center;
  margin: 128px auto;
  border-radius: 10px;
  padding-bottom: 32px;
}
h1 {
  margin: 32px 0;
  color:white;
  font-size: 24px;
}
.todo-form {
  margin-bottom: 32px;
}
.todo-input {
  padding: 14px 32px 14px 16px;
  border-radius: 4px 0 0 4px;
  border: 2px solid #5d0cff;
  outline: none;
  width: 320px;
  background: transparent;
  color: #fff;
}
.todo-input::placeholder {
  color: #fff;
}
.todo-button {
  padding: 16px;
  border: none;
  border-radius: 0 4px 4px 0;
  cursor: pointer;
  background: linear-gradient(
    90deg,
    rgba(93, 12, 255, 1) 0%,
    rgba(155, 0, 250, 1) 100%
  );
  color: #fff;
}
.todo-input.edit {
  border: 2px solid #149fff;
 
}
.todo-button.edit {
  background: linear-gradient(
    90deg,
    rgba(20, 159, 255, 1) 0%,
    rgba(17, 122, 255, 1) 100%
  );
  padding: 16px 22px;
}
.todo-container {
  display: flex;
  flex-direction: row;
  position: relative;
}
.todo-row {
  display: flex;
  justify-content: space-between;
  margin: 4px auto;
  color:white;
  background: 
  linear-gradient(to bottom left, #333399 0%, #00ccff 100%); padding: 16px;
  border-radius: 5px;
  width: 90%;
}
.todo-row:hover {
 
  background: 
  linear-gradient(to bottom left, #5a475c 0%, #080808 100%);

}

.icons {
  display: flex;
  align-items: center;
  font-size: 24px;
  cursor: pointer;
}
.edit-icon {
  color: #fff;
}
.edit-icon:hover{
  color: #000000;
}
.delete-icon {
  margin-right: 5px;
  color: white;
  
}
.delete-icon:hover{
  color: rgb(173, 34, 34);
}